<script>
	import {onMount} from 'svelte';
	import seedrandom from 'seedrandom';
	const numBooks = 9;
	let books = [];
	let seed = getSeed();
	$: pageUrl = getPageUrl(seed);
	async function fetchJson(url) {
		let response = await fetch(url);
		return await response.json();
	}
	function getSeed() {
		let url = new URL(document.location);
		if (url.hash !== "") {
			return parseInt(url.hash.substr(1));
		}
		return Date.now();
	}
	function getPageUrl(seed) {
		let url = new URL(document.location);
		url.hash = seed;
		return url;
	}
	onMount(async () => {
		let metadata = await fetchJson("data/meta.json");
		let totalBooks = Object.values(metadata).reduce((a, b) => a + b);
		let bookIds = new Set();
		let rng = seedrandom(seed);
		while (bookIds.size < numBooks) {
			bookIds.add(Math.floor(rng() * totalBooks));
		}
		bookIds = [...bookIds].sort((a, b) => a - b);
		console.log(bookIds);
		let currentCount = 0;
		for (let category in metadata) {
			let count = metadata[category];
			if (bookIds.length == 0) {
				break;
			}
			if (currentCount + count > bookIds[0]) {
				let data = await fetchJson(`data/${category}.json`)
				while (bookIds.length > 0 && currentCount + count > bookIds[0]) {
					books = [...books, data[bookIds[0] - currentCount]];
					bookIds = bookIds.slice(1);
				}
			}
			currentCount += count;
		}
	});
</script>

<main>
<h1>瞎读</h1>
<p class="subtitle">不喜欢？<a href=".">再选几本！</a></p>
<div class="content">
{#each books as book}
	<div class="book">
		<a href={book.bookUrl}>
			<img src={book.cover} alt={book.title}/>
			<p class="title">{book.title}</p>
		</a>
		<p class="author">作者: {book.author}</p>
	</div>
{/each}
</div>
<p>这次随机结果的链接: <a href={pageUrl}>{pageUrl}</a> </p>
</main>

<style>
	main {
		margin: 0 auto;
		text-align: center;
		max-width: 600px;
	}

	.content {
			display: grid;
			grid-template-columns: repeat(3, minmax(120px, 1fr) );
	}

	.book {
		padding: 4px;
	}
	.title {
		margin-bottom: 0px;
	}
	.author {
		margin-top: 5px;
		font-size: 10px;
	}
</style>